<template>
    <div class="cardDetails">
        <img :src="card.image_uris.png" alt="">
        <h2>{{ card.name }}</h2>
        <p><b>Text</b> <span v-html="text"></span></p>
        <p><b>Price</b> &euro; {{ card.prices.eur }}</p>
        <p><b>Legal in</b> {{ legalList(card) }}</p>
    </div>
</template>

<script>
import parseMana from "../js/parseMana.js";

export default {
    props: ["card"],
    computed: {
        text() {
            return parseMana(this.card.oracle_text);
        }
    },
    methods: {
        legalList(card) {
            var entries = Object.entries(card.legalities);

            var list = [];

            entries.forEach(ruling => {
                if (ruling[1] === "legal") {
                    list.push(ruling[0]);
                }
            });

            return list.join(", ");
        }
    }
}
</script>

<style lang="scss" scoped>

@import "../mdl-vue/config.scss";

@font-face {
    font-family: "Ringbearer";
    src: url(../fonts/Ringbearer.ttf);
}

.cardDetails {
    width: 100%;

    img {
        max-width: 256px;

        margin: 16px auto;
        display: block;

        box-shadow: $elevation24dp;
        border-radius: 12px;
    }

    h2 {
        font-family: "Ringbearer";
        font-size: dpToRem(32);
        text-align: center;
    }
}

</style>