import { replaceAll } from "./global.js";

const numbers = [];

for (var i = 0; i <= 20; i++) {
    numbers.push(i.toString());
}

const iconCodes = [
    ...numbers,
    "r",
    "g",
    "b",
    "w",
    "u",
    ["t", "tap"]
]

export default function(text) {
    iconCodes.forEach(code => {
        if (Array.isArray(code)) {
            text = replaceAll(text, `{${code[0].toUpperCase()}}`, `<span class="ms ms-${code[1]}"></span>`);
        } else {
            text = replaceAll(text, `{${code.toUpperCase()}}`, `<span class="ms ms-${code}"></span>`);
        }
    });

    return text;
}